.singleProductContainer {
    display: flex;
    padding: 20px;
    justify-content: center;
    width: 80%;
    margin: 0 auto;
    column-gap: 30px;
    flex-wrap: wrap;
}

.btnsSignleProduct {
    display: flex;
    column-gap: 20px;
    justify-content: center;
    align-items: center;
}

.productImagesAndBtns {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    justify-content: center;
    align-items: center;
    /* box-shadow: inset 0 0 15px 5px rgba(0, 0, 0, 0.466); */
    padding: 20px;
    background-color: rgba(44, 166, 197, 0.164);
    border-radius: 5px;
    border-radius: 3px;
    max-height: 400px;
    flex-shrink: 0;
    /* margin-top: 100px; */
}

.ImageForSingleProduct {
    height: 300px;
    width: 200px;
    flex-shrink: 0;
    object-fit: cover;
}

.ProductInfoSingleProduct {
    padding: 20px;
    min-width: 300px;
    flex: 1;
}

.backgroundRattingForSingleProduct {
    background-color: green;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: white;
    width: fit-content;
    padding: 2px 6px;
    column-gap: 2px;
    border-radius: 3px;
    margin-right: 10px;
}

table {
    border-collapse: collapse;
    width: 100%;
}

td {
    border: 1px solid black;
    width: 250px;
    text-align: center;
}

tr:nth-child(2n+1) {
    background-color: rgb(239, 246, 250);
}

.headerTablePriceCompareSingleProduct {
    background: rgb(19, 44, 60);
    background: linear-gradient(194deg, rgba(19, 44, 60, 1) 39%, rgba(0, 48, 143, 1) 100%);
    color: white;
    text-transform: uppercase;
    font-weight: 500;
    padding: 5px 0;
}

.ProductDiscriptionForSingleProduct {
    border: 1px solid rgba(121, 121, 121, 0.664);
    padding: 20px;
    margin-top: 20px;
    padding-top: 0;
}

.writeReview {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.alert {
    position: fixed;
    top: 20%;
    width: 100%;
    transform: translateX(-50%);
    left: 50%;
    z-index: 5;
}

@media only screen and (max-width: 600px) {
    .ProductInfoSingleProduct {
        padding: 0;
        text-align: center;
    }
}